import IXml from './IXml';
import IFeatureXml from './IFeatureXml';
import IIcon from './IIcon';
import IOpenDocumentConventionXml from './IOpenDocumentConventionXml';

interface IPackageXml {
  manifest: IOpenDocumentConventionXml;
  features: IFeatureXml[];
  relationships: IXml;
  contentTypesXml: IXml;
  customFiles: IXml[];
  icon?: IIcon;
}

export default IPackageXml;
